import { BootstrapModalModule } from 'ng2-bootstrap-modal';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule as AngularFormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppTranslationModule } from '../../app.translation.module';
import { NgaModule } from '../../theme/nga.module';
import { NgbRatingModule } from '@ng-bootstrap/ng-bootstrap';
import { DataTableModule } from 'angular2-datatable';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { routing } from './pagoliquidacions.routing';
import { PagoliquidacionsComponent } from './pagoliquidacions.component';
import { PagoliquidacionsService } from './components/pagoliquidacions-table/pagoliquidacions.service';
import { PagoliquidacionsTableComponent } from './components/pagoliquidacions-table/pagoliquidacions-table.component';

@NgModule({
  imports: [
    CommonModule,
    AngularFormsModule,
    AppTranslationModule,
    ReactiveFormsModule,
    NgaModule,
    NgbRatingModule,
    routing,
    DataTableModule,
    NgbModalModule,
    BootstrapModalModule.forRoot({ container: document.body })
  ],
  declarations: [
    PagoliquidacionsComponent,
    PagoliquidacionsTableComponent,
  ],
  entryComponents: [
  ],
  providers: [
    PagoliquidacionsService
  ]
})
export class PagoliquidacionsModule {
}



